.Home1{
    align-content: center;
    color: aliceblue;
    text-align: center;
    font-family: "Times New Roman", Times, serif;
    position: absolute;
    font-variant: small-caps;
    background: rgba(0, 0, 0, 0.767);
    color: white;
    width: 100%;
    text-align: center;
    margin: auto;
    font-size: 30px;
    line-height: 40px;
    letter-spacing: 5px;
    text-shadow: -2px 0 0 #fdff2a,
      -4px 0 0 #df4a42,
      2px 0 0 #91fcfe,
      4px 0 0 #4405fc;
    animation: blink .5s steps(4, start) infinite;
    margin-bottom: 2rem;
    
}
   @keyframes blink{
     to{
       visibility: hidden;
  }}
  
.main{
  display: flex;
  flex-direction: row;
  width: 100%;
}
.home-container1,.home-container2,.home-container3,.home-container4,.home-container5,.home-container6{
  position:absolute;
    top: 22%;
    left: 8%;
    justify-content: center;
    align-items: flex-start;
    border-radius: 25px;
    width: 25%;
    overflow:hidden;
    box-shadow: 0px 0px 15px -5px;
    backdrop-filter: blur(10px);
    transition: 0.3s;
    animation: ease;
    cursor: pointer;
    display:block;
}
.home-container2{
  left: 38%;
  margin-bottom: 5rem;
}
.home-container3{
  left: 68%;
  margin-bottom: 5rem;
}
.home-container4{
  top: 80%;
  margin-bottom: 5rem;
  
}
.home-container5{
  top: 80%;
  left: 38%;
}
.home-container6{
  top: 80%;
  left: 68%;
}
.home-container1:hover,.home-container2:hover,.home-container3:hover,.home-container4:hover,.home-container5:hover,.home-container6:hover{
  transform: scale(1.05);
  box-shadow: 0px 0px 15px 0px;
}
.image-container{
    display: flex;
    flex-direction: column;
    size: inherit;
    overflow: hidden;
    height:50%;
    margin-bottom: 2%;
}
.home-content{
  margin: 1%;

}
.title{
  margin-bottom: .5%;
}
.btn{
  display: flex;
  justify-content: center;
}
.btn button{
  padding: 0.5rem;
  width: 80px;
  height: 40px;
  background-color: rgba(0, 0, 0, 0.199);
 
  border: none;
  transition: 0.2s;
  margin-bottom: 0.5rem;
  border-radius: 5px;
  
}
.btn:hover{
  background: rgba(0, 0, 0, 0.1);
}
.btlink{
  text-transform: uppercase;
  font-size: 15px;
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif; 
  color:rgb(255, 255, 255) ;
  text-decoration: none;
  font-weight: bold;
}
.title{
  margin-top: 0.5rem;
  color: aliceblue;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  padding: 5px;
  font-size: 1vw;
  justify-content: space-around;
  
}
.body{
  color: aliceblue;
  background-color: rgba(0, 0, 0, 0.295);
  font-size: 0.8vw;
  font-weight: 200;
}